/* Navbar */
.collapse{
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;
}
.navbar-expand-lg{
  border-bottom: 2px solid black;
}
.Nav-head{
  font-size: 2rem;
}
/* Home Component  */
.Top-Section {
  padding: 2rem;
  display: flex;
  background-color: rgba(214, 212, 210, 0.329);
}
.navigation {
  font-size: 1.3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.port:hover {
  transform: scale(1.2);
  box-shadow: 1px 1px 15px rgb(107, 102, 102);
}
.port {
  margin-top: 5%;
  width: 60%;
  border-radius: 40%;
  box-shadow: 0px 0px 20px black;
}
.box {
  width: 30%;
  height: auto;
}

.nitin {
  box-shadow: 0 3px rgba(0, 0, 0, 0.647);
}

@media only screen and (max-width: 1024px) {
  /* Top Home Justification  */
  .Top-Section {
    display: block;
    text-align: center;
  }
  .port {
    margin: 7% 0;
    width: 30%;
    border-radius: 40%;
    box-shadow: 0px 0px 20px black;
  }
  .title_home {
    font-size: 1.5rem;
  }
  .res_pro {
    font-size: 2rem;
    margin: 0;
    border-radius: 1rem;
    color: black;
    background-color: rgb(178, 182, 182);
  }

  /* About Component  */
  .img_container {
    width: 30%;
  }
  .Nitin_Img {
    width: 90%;
    border-radius: 20%;
    box-shadow: 5px 5px 20px black;
  }
  .about_info {
    font-size: 1rem;
  }

  /* project Responsive  */
  .roll{
    margin-top: 3%;
  }
  .pro_info{
    font-size: 1rem;
  }

  /* Skill Section  */
}

@media only screen and (max-width: 768px) {
  /* Top Home Justification  */
  .word_home {
    font-size: 1.7rem;
  }
  .Top-Section {
    display: block;
    text-align: center;
  }
  .port {
    margin: 7% 0;
    width: 40%;
    border-radius: 40%;
    box-shadow: 0px 0px 20px black;
  }
  .title_home {
    font-size: 1.3rem;
    /* text-align: justify; */
  }
  .res_pro {
    font-size: 2rem;
    margin: 0;
    border-radius: 1rem;
    color: black;
    background-color: rgb(178, 182, 182);
  }

  /* About Component  */

  .img_container {
    width: 100%;
  }
  .Nitin_Img {
    width: auto;
    height: 70%;
    height: 50%;
    border-radius: 20%;
    box-shadow: 5px 5px 20px black;
  }
  .about_info {
    font-size: 0.9rem;
  }

  /* Footer Responsive */
  .foot{
    display: block;
  }

  /* Skills  */
  .logo {
    height: 7rem !important;
    width: 7rem !important;
    margin: 1.3rem;
  }
  /* Skills End  */

  /* project Responsive  */
  .roll{
    margin-top: 3%;
  }
  .pro_info{
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 440px) {
  /* Top Home Justification  */
  .word_home {
    font-size: 1.5rem;
  }
  .port {
    margin: 20% 0 7% 0;
    width: 65%;
    border-radius: 40%;
    box-shadow: 0px 0px 20px black;
  }
  .title_home {
    font-size: 1.2rem;
  }
  .res_pro {
    width: 100%;
    font-size: 1rem;
    margin: 0;
    border-radius: 1rem;
    color: black;
    background-color: rgb(178, 182, 182);
  }
  .img_container {
    display: none;
  }
  .about_info {
    font-size: 1.05rem;
  }
  .intro_about{
    text-align: justify;
  }
  /* project Responsive  */
  .roll{
    margin-top: 3%;
  }
  .pro_info{
    font-size: 0.5rem;
  }

   /* Skills  */
   .logo {
    height: 5rem !important;
    width: 5rem !important;
    margin: 1.3rem;
  }
  /* Skills End  */

  /* Footer  */
  .Link-a{
    font-size: 1.2rem !important;
    margin: 0;
  }
  /* Footer end */
}

@media only screen and (max-width: 380px) {
  .title_home{
    font-size: 1rem;
    text-align: justify;
  }
  .intro_about{
    text-align: justify;
  }
}

/* Project Component  */
.project {
  background-color: rgba(9, 2, 2, 0.787);
  color: white;
}
.res_pro {
  font-size: 2rem;
  margin-top: 2rem;
  border-radius: 1rem;
  color: black;
  background-color: rgb(178, 182, 182);
}

/* About Component  */
.about {
  display: flex;
  background-color: rgba(214, 212, 210, 0.394);
}
.Nitin_Img {
  width: 70%;
  border-radius: 20%;
  box-shadow: 5px 5px 20px black;
}
.img_container {
  width: 30%;
}

/* Skill Componenets  */
.skill {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: rgba(214, 212, 210, 0.394);
}
.logo:hover {
  transform: scale(1.2);
  box-shadow: 1px 1px 15px rgb(107, 102, 102);
}
.logo {
  border-radius: 30%;
  background-color: white;
  border: 1px solid gray;
  height: 10rem;
  width: 10rem;
  margin: 1.3rem;
}

/* Footer Css  */

.fo_img {
  width: 2.3rem;
  border-radius: 50%;
}
.fo_im_foo{
  width: 4rem;
  border-radius: 50%;
}
.footer-contact{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.fo_img:hover {
  transform: scale(1.2);
  box-shadow: 1px 1px 15px rgb(107, 102, 102);
}
.fo_imgage {
  margin-left: 1rem;
}
